import React, { Component } from 'react';
import {Link} from 'react-router-dom';

import OrderReservedRow from './OrderReservedRow';

import shopConfig from '../../../../Config/ShopConfig';

export default class OrderReserved extends Component {

    render () {

        return (
            <div className="wrapper order-wrapper">
                <section className="order-done">
                    <h2 className="order-done__title order-process__title"> , !</h2>
                    <div className="order-done__information order-info">

                        <OrderReservedRow title=" :">
                            { this.props.price }<i className="fa fa-rub" aria-hidden="true"/>
                        </OrderReservedRow>

                        <OrderReservedRow title=" :">
                            { shopConfig.variantsPaid.filter(item => item.value === this.props.paymentType)[0].name }
                        </OrderReservedRow>

                        <OrderReservedRow title=" :">
                            { this.props.name }
                        </OrderReservedRow>

                        <OrderReservedRow title=" :">
                            { this.props.address }
                        </OrderReservedRow>

                        <OrderReservedRow title=":">
                            { this.props.phone }
                        </OrderReservedRow>
                    </div>
                    <p className="order-done__notice">      <span>{this.props.email}</span></p>
                    <Link to="/catalog/0" className="order-done__continue"> </Link>
                </section>
            </div>
        );
    }
}




