import React, { Component } from 'react';
import $ from 'jquery';

import TextInput from './TextInput';
import OrderRadioGroup from './OrderRadioGroup';

import shopConfig from '../../../../../Config/ShopConfig';

export default class OrderFormInfo extends Component {

    $orderInfo;

    onConfirmOrder(event) {

        if(this.$orderInfo && this.$orderInfo.length !== 0 && this.$orderInfo[0].checkValidity()) {

            const orderInfo = this.getOrderInfo();

            if(this.props.orderRegistration)
                this.props.orderRegistration(orderInfo);

        } else {
            alert("    !!!")
        }

        event.preventDefault();
    }

    getOrderInfo() {

        const name = this.$orderInfo.find('input[name="name"]').val();
        const phone = this.$orderInfo.find('input[name="phone"]').val();
        const address = this.$orderInfo.find('input[name="address"]').val();
        const email = this.$orderInfo.find('input[name="email"]').val();

        const paymentType = this.$orderInfo.find('input[name="paid"]').val();

        return {name, phone, email, address, paymentType};
    }

    render () {

        return (
            <div className="order-process__confirmed">
                <form action="orderInfo" ref={ref => this.$orderInfo = $(ref)} >
                    <div className="order-process__delivery">
                        <h3 className="h3">   ?</h3>
                        <div className="order-process__delivery-form">

                            <TextInput title="" name="name" placeholder=", "/>
                            <TextInput title="" name="phone" type="tel" placeholder="   "/>
                            <TextInput title="Email" name="email" tyoe="email" placeholder=" email"/>
                            <TextInput title="" name="address" placeholder="      "/>

                        </div>
                        <p>    .         .</p>
                    </div>
                    <OrderRadioGroup title="      ?" name="paid" items={shopConfig.variantsPaid} />
                    <button className="order-process__form-submit order-process__form-submit_click" onClick={this.onConfirmOrder.bind(this)} > </button>
                </form>
            </div>
        );
    }
}




